@import "../../../styles/vars";
@import "../../../styles/mixins";

.technologiesBlock {
  @include setDistance(padding-top, 70);
  @include setDistance(padding-bottom, 70);
  overflow: hidden;

  &__title {
    text-align: center;
    margin-bottom: 40px;

    @include lgScreenMedia {
      margin-bottom: 30px;
    }

    @include lgScreenMedia {
      margin-bottom: 20px;
    }
  }

  &__contentText {
    max-width: 825px;
    color: $color-white;
    margin-left: auto;
    margin-right: auto;
    text-align: center;

    @include xlScreenMedia {
      max-width: 725px;
    }
  }

  &__iconsBlock {
    @include flexCol();
    margin-top: 30px;

    @include mdScreenMedia {
      justify-content: flex-start;
      gap: 20px;
      width: 940px;
      position: relative;
      height: 45px;
      margin-top: 20px;
    }
  }

  &__marque {
    @include flexCol();
    gap: 40px;

    @include xlScreenMedia {
      gap: 20px;
    }

    @include lgScreenMedia {
      gap: 15px;
    }

    @include mdScreenMedia {
      justify-content: flex-start;
      gap: 20px;
      animation: marque 18s linear infinite;
      position: absolute;
    }

    &_2 {
      @media (min-width: $md-screen) {
        display: none;
      }
      animation-delay: 9s;
      transform: translateX(1150px)
    }
  }

  &__iconPicture {
    max-width: 100%;
  }

  &__iconImg {
    width: 100%;
  }

}


@keyframes marque {
  from {
    transform: translateX(645px)
  }
  to {
    transform: translateX(-1280px)
  }
}